body {
  backgrond: rgba(0, 0, 0, 0.1);
  font-family: monaco;
}

.center {
  max-width: 1350px;
  margin: 0 auto;
  padding: 20px;
}

.holder {
  display: flex;
}

.holder > div {
  flex: 1;
  padding: 20px;
}

h1 {
  text-align: center;
}

#editor {
  border: 1px solid rgba(128, 128, 128, 0.5);
}

.CodeMirror {
  height: 400px;
  font-family: Monaco;
  width: 100%;
  border: 2px solid grey;
}




